// implement a component here

interface Props {
  tabs: Tab[],
  onTabSelected: (id: string) => void,
}

export const Tabs: React.FC<Props> = ({ tabs, onTabSelected }) => {
  return (
    <ul>
      {tabs.map((tab: Tab) => (
        <li key={tab.id}>
          <title>{tab.title}</title>
          <p data-cy="tab-content">{tab.content}</p>
          <button
            type="button"
            onClick={() => {
              onTabSelected(tab.id);
            }}
          >
            Select
          </button>
        </li>
      ))}
    </ul>
  );
};
